import { Typography } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import Backdrop from '@material-ui/core/Backdrop';
import Button from '@material-ui/core/Button';
import Fade from '@material-ui/core/Fade';
import Modal from '@material-ui/core/Modal';
import Paper from '@material-ui/core/Paper';
import Popover from '@material-ui/core/Popover';
import { withStyles } from '@material-ui/core/styles';
import LocationOnIcon from '@material-ui/icons/LocationOnRounded';
import React, { useState, ReactElement } from 'react';

import ChartView from '../containers/chartContainer';

export interface Props {
  classes: any;
  name: string;
  stationId: string;
  key: string;
  lat: number;
  lng: number;
  stationDeviceList: [];
  getDeviceList: Function;
  getStationData: Function;
  addDevice: Function;
}

const styles: object = theme => ({
  stationContainer: {
    display: 'flex',
    flexDirection: 'column',
    padding: '1em 0 1em 0',
    width: 180,
    backgroundColor: theme.palette.primary.light,
    boxShadow:
      '0px 2px 4px -1px rgba(0,0,0,0.2),' +
      '0px 4px 5px 0px rgba(0,0,0,0.14),' +
      '0px 1px 10px 0px rgba(0,0,0,0.12)',
  },
  stationTitle: {
    margin: 4,
    textAlign: 'center',
    color: theme.palette.primary.contrastText,
    fontSize: 13.5,
  },
  stationButton: {
    margin: 12,
    backgroundColor: theme.palette.secondary.main,
    '&:hover': {
      backgroundColor: theme.palette.secondary.dark,
    },
  },
  stationIcon: {
    position: 'relative',
    left: '-0.5em',
    top: '-1em',
    color: theme.palette.secondary.main,
  },
  deviceTextField: {
    paddingLeft: 8,
    paddingRight: 8,
  },
  deviceInput: {
    backgroundColor: theme.palette.background.default,
  },
  modal: {
    height: 680,
  },
  [theme.breakpoints.down('lg')]: {
    modal: {
      width: '100%',
      marginTop: '5em',
    },
  },
  [theme.breakpoints.up('lg')]: {
    modal: {
      width: 1200,
      margin: '0 auto',
      marginTop: '10em',
    },
  },
});

const StationView = ({
  classes,
  name,
  stationId,
  stationDeviceList,
  getDeviceList,
  getStationData,
  addDevice,
}: Props): React.ReactElement => {
  const [anchorEl, setAnchorEl] = useState<SVGSVGElement | null>(null);
  const [
    deviceAnchorEl,
    setDeviceAnchorEl,
  ] = useState<HTMLButtonElement | null>(null);
  const [openChart, setOpenChart] = useState(false);
  const [deviceName, setDeviceName] = useState('');
  const open = Boolean(anchorEl);
  const deviceOpen = Boolean(deviceAnchorEl);
  const id = open ? 'simple-popover' : undefined;

  const handleClick = (event: React.MouseEvent<SVGSVGElement>): void => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = (): void => {
    setAnchorEl(null);
  };

  const handleClickDevice = (
    event: React.MouseEvent<HTMLButtonElement>
  ): void => {
    getDeviceList(stationId);
    setDeviceAnchorEl(event.currentTarget);
  };

  const handleCloseDevice = (): void => {
    setDeviceAnchorEl(null);
  };

  const handleOpenChart = (): void => {
    setOpenChart(true);
  };

  const handleCloseChart = (): void => {
    setOpenChart(false);
  };

  const handleClickData = (): void => {
    getStationData(stationId);
    handleOpenChart();
  };

  const handleClickAddDevice = (
    event: React.MouseEvent<HTMLButtonElement>
  ): void => {
    console.log(event);
    addDevice({
      name: deviceName,
      type: 'Ultrasonic',
      stationName: name,
      description: '',
    });
  };

  return (
    <>
      <Popover
        id={id}
        open={deviceOpen}
        anchorEl={deviceAnchorEl}
        onClose={handleCloseDevice}
        anchorOrigin={{
          vertical: 'center',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'center',
          horizontal: 'left',
        }}
      >
        <Paper className={classes.stationContainer}>
          <Typography className={classes.stationTitle}>ENHET</Typography>
          {stationDeviceList.length > 0 &&
            stationDeviceList.map((d: any) => (
              <Button
                key={d.id}
                variant="contained"
                className={classes.stationButton}
                onClick={handleClickData}
              >
                {d.name}
              </Button>
            ))}
          {stationDeviceList.length <= 0 && (
            <>
              <TextField
                id="deviceName"
                label="Enhet navn"
                className={classes.deviceTextField}
                type="text"
                autoComplete="current-password"
                margin="normal"
                variant="filled"
                value={deviceName}
                onChange={event => setDeviceName(event.target.value)}
                InputLabelProps={{
                  classes: {
                    root: classes.stationInputLabel,
                  },
                }}
                InputProps={{
                  classes: {
                    input: classes.deviceInput,
                  },
                }}
              />
              <Button
                variant="contained"
                className={classes.stationButton}
                onClick={handleClickAddDevice}
              >
                Legg til
              </Button>
            </>
          )}
        </Paper>
      </Popover>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'center',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'center',
          horizontal: 'left',
        }}
      >
        <Paper className={classes.stationContainer}>
          <Typography className={classes.stationTitle}>
            {name.toUpperCase()}
          </Typography>
          <Button
            variant="contained"
            className={classes.stationButton}
            onClick={handleClickData}
          >
            Data
          </Button>
          <Button
            variant="contained"
            className={classes.stationButton}
            onClick={handleClickDevice}
          >
            Enhet
          </Button>
        </Paper>
      </Popover>
      <LocationOnIcon
        fontSize={'large'}
        className={classes.stationIcon}
        onClick={handleClick}
      />
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={openChart}
        onClose={handleCloseChart}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={openChart}>
          <ChartView />
        </Fade>
      </Modal>
    </>
  );
};

export default withStyles(styles)(StationView);
