import DateFnsUtils from '@date-io/date-fns';
import AppBar from '@material-ui/core/AppBar';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import { withStyles } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import TimelineIcon from '@material-ui/icons/Timeline';
import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from '@material-ui/pickers';
import React, { useEffect } from 'react';
import { Scatter } from 'react-chartjs-2';

import { Color } from '../styles/styles';
import { dateToDayBegin, dateToDayEnd } from '../utils/converter';

export interface Props {
  classes: any;
  data: [];
  weatherData: [];
  xMin: string;
  xMax: string;
  updateXMin: Function;
  updateXMax: Function;
}

const styles: object = theme => ({
  chartContainer: {
    backgroundColor: theme.palette.background.default,
    padding: 8,
    margin: 8,
  },
  chartMenu: {
    backgroundColor: theme.palette.secondary.main,
  },
  chartDatePickerInput: {
    marginLeft: theme.spacing(2),
    color: theme.palette.secondary.contrastText,
    '&:before': {
      borderBottom: `1px solid ${theme.palette.background.default}`,
    },
    '&:after': {
      borderBottom: `2px solid ${theme.palette.background.default}`,
    },
    '&:hover:not(.Mui-disabled)::before': {
      borderBottom: `2px solid ${theme.palette.background.default}`,
    },
  },
  chartDatePicker: {
    marginLeft: theme.spacing(2),
    color: theme.palette.secondary.contrastText,
    '&.Mui-focused': {
      color: theme.palette.secondary.contrastText,
    },
  },
  chartArrowContainer: {
    display: 'flex',
    justifyContent: 'space-between',
    margin: '0 1.2em 0.2em 1.2em',
    color: theme.palette.secondary.main,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
});

const ChartView = ({
  classes,
  data,
  weatherData,
  xMin,
  xMax,
  updateXMin,
  updateXMax,
}: Props): React.ReactElement => {
  const initDate = dateToDayBegin(new Date());
  const maxLevel = 300;
  const [selectedDate, setSelectedDate] = React.useState<Date | null>(initDate);
  const [mouseDown, setMouseDown] = React.useState<boolean>(false);
  const [cursorMove, setCursorMove] = React.useState<object>({});
  const handleDateChange = (date: Date | null): void => {
    setSelectedDate(dateToDayBegin(date));
    updateXMin(dateToDayBegin(date).toISOString());
    updateXMax(dateToDayEnd(date).toISOString());
  };
  // if (data.find((d: any) => d.value >= 100)) {
  //   console.log('abc');
  //   maxLevel = 1000;
  // }
  const chartData = {
    labels: ['Point'],
    datasets: [
      {
        label: 'Avlpsniv',
        yAxisID: 'y-axis-1',
        pointStyle: 'circle',
        fill: true,
        pointRadius: 2,
        pointBorderWidth: 2,
        backgroundColor: Color.teal100,
        pointBorderColor: Color.teal100,
        pointBackgroundColor: Color.teal100,
        pointHoverRadius: 3,
        pointHoverBorderWidth: 1,
        pointHoverBackgroundColor: Color.teal900,
        pointHoverBorderColor: Color.teal900,
        pointHitRadius: 10,
        data: data.map((d: any) => {
          return {
            x: new Date(d.id),
            y: d.value * 10,
          };
        }),
      },
      {
        label: 'Nedbr',
        yAxisID: 'y-axis-2',
        fill: true,
        pointStyle: 'rectRot',
        pointRadius: 2,
        pointBorderWidth: 2,
        backgroundColor: Color.purple100,
        pointBorderColor: Color.purple100,
        pointBackgroundColor: Color.purple100,
        pointHoverRadius: 3,
        pointHoverBorderWidth: 1,
        pointHoverBackgroundColor: Color.purple900,
        pointHoverBorderColor: Color.purple900,
        pointHitRadius: 10,
        data: weatherData.map((d: any) => {
          return {
            x: new Date(d.id),
            y: d.precipitation,
          };
        }),
      },
    ],
  };

  const options = {
    scales: {
      xAxes: [
        {
          type: 'time',
          time: {
            unit: 'hour',
          },
          ticks: {
            min: new Date(xMin),
            max: new Date(xMax),
          },
        },
      ],
      yAxes: [
        {
          // type: 'linear',
          display: true,
          position: 'left',
          id: 'y-axis-1',
          gridLines: {
            display: false,
          },
          labels: {
            show: true,
          },
          ticks: {
            min: 0,
            max: maxLevel,
            callback: (value: number): string => `${value}mm`,
          },
        },
        {
          // type: 'linear',
          display: true,
          position: 'right',
          id: 'y-axis-2',
          gridLines: {
            display: false,
          },
          labels: {
            show: true,
          },
          ticks: {
            min: 0,
            max: 10,
            callback: (value: number): string => `${value}mm`,
          },
        },
      ],
    },
    // events: [''],
  };

  const legend = {
    pointStyle: 'star',
  };

  useEffect(() => {
    handleDateChange(initDate);
  }, []);

  return (
    <div className={classes.chartContainer}>
      <AppBar position="static" className={classes.chartMenu}>
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="menu"
          >
            <TimelineIcon />
          </IconButton>
          <Typography variant="h6" className={classes.title}>
            Punktdiagram:avlpsniv
          </Typography>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <Grid container justify="flex-end">
              <KeyboardDatePicker
                disableToolbar
                variant="inline"
                format="dd.MM.yyyy"
                margin="normal"
                id="date-picker-to"
                label="Date"
                value={selectedDate}
                onChange={handleDateChange}
                className={classes.chartDatePicker}
                InputLabelProps={{
                  classes: {
                    root: classes.chartDatePicker,
                  },
                }}
                InputProps={{
                  classes: {
                    root: classes.chartDatePickerInput,
                  },
                }}
                KeyboardButtonProps={{
                  'aria-label': 'change date',
                  classes: {
                    root: classes.chartDatePicker,
                  },
                }}
              />
            </Grid>
          </MuiPickersUtilsProvider>
        </Toolbar>
      </AppBar>
      <div
        style={cursorMove}
        onMouseDown={(): void => {
          setCursorMove({ cursor: 'move' });
          setMouseDown(true);
        }}
        onMouseUp={(): void => {
          setCursorMove({ cursor: '' });
          setMouseDown(false);
        }}
        onMouseMove={(event): void => {
          if (mouseDown) {
            const distance = Math.floor(Math.abs(event.movementX) / 18);
            let direction = event.movementX > 0 ? -1 : 1;
            direction = event.movementX === 0 ? 0 : direction;
            const newXMin = new Date(xMin);
            const newXMax = new Date(xMax);
            newXMin.setHours(newXMin.getHours() + distance * direction);
            newXMax.setHours(newXMax.getHours() + distance * direction);
            updateXMin(newXMin);
            updateXMax(newXMax);
            setSelectedDate(newXMax);
          }
        }}
      >
        <Scatter data={chartData} options={options} legend={legend} />
      </div>
      <div className={classes.chartArrowContainer}>
        <IconButton
          color="secondary"
          onClick={(): void => {
            const newXMin = new Date(xMin);
            const newXMax = new Date(xMax);
            newXMin.setHours(newXMin.getHours() - 1);
            newXMax.setHours(newXMax.getHours() - 1);
            updateXMin(newXMin);
            updateXMax(newXMax);
            setSelectedDate(newXMax);
          }}
        >
          <ArrowBackIosIcon />
        </IconButton>
        <IconButton
          color="secondary"
          onClick={(): void => {
            const newXMin = new Date(xMin);
            const newXMax = new Date(xMax);
            newXMin.setHours(newXMin.getHours() + 1);
            newXMax.setHours(newXMax.getHours() + 1);
            updateXMin(newXMin);
            updateXMax(newXMax);
            setSelectedDate(newXMax);
          }}
        >
          <ArrowForwardIosIcon />
        </IconButton>
      </div>
    </div>
  );
};

export default withStyles(styles)(ChartView);
