import { connect } from 'react-redux';
import StationView from '../components/stationView';
import {
  getStationData,
  getStationDeviceList,
  addStationDevice,
} from '../actions/stationActions';

export default connect(
  (state: any, props) => ({
    stationDeviceList: state.station.stationDeviceList,
  }),
  (dispatch: Function, props) => ({
    getDeviceList: (stationId: string): Function =>
      dispatch(getStationDeviceList(stationId)),
    getStationData: (stationId: string): Function =>
      dispatch(getStationData(stationId)),
    addDevice: (deviceName: string): Function =>
      dispatch(addStationDevice(deviceName)),
  })
)(StationView);
